import { Pembelian_item } from 'src/app/sdk/asset/models/pembelian-_item';
import { Pembelian } from 'src/app/sdk/asset/models/pembelian';
import { filter } from 'rxjs/operators';
import { Component, OnInit } from '@angular/core';
import { DirektoriPegawaiMasterService, CompanyKantorService } from 'src/app/sdk/hr/services';
import { VendorVendorService, AssetModelxService, GaPembelianService, GaPembelianItemService } from 'src/app/sdk/asset/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService, NzDrawerService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Pegawai_master, Kantor } from 'src/app/sdk/hr/models';
import { Modelx, Vendor } from 'src/app/sdk/asset/models';
import { FormControl } from '@angular/forms';
import { SearchItemComponent } from 'src/app/components/search-item/search-item/search-item.component';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private gaPembelianService: GaPembelianService,
        private vendorVendorService: VendorVendorService,
        private gaPembelianItemService: GaPembelianItemService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private companyKantorService: CompanyKantorService,
        private assetModelxService: AssetModelxService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private drawerService: NzDrawerService,
        private activatedRoute: ActivatedRoute,
        private singleUploadService: SingleUploadService,
    ) { }

    toogleUpload = false
    dataPembelian: Pembelian = {}
    list_item: Pembelian_item[] = []
    id_pembelian: string = ''

    listPegawai: Pegawai_master[] = []
    listKantor: Kantor[] = []
    listModel: Modelx[] = []
    listVendor: Vendor[] = []

    ngOnInit() {
        this.id_pembelian = this.activatedRoute.snapshot.paramMap.get('id_pembelian')
        this.getPreeData()
        this.form()
        this.getData()
        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'ga_issued.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }

    getPreeData() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(data => {
            this.listPegawai = data.data
        })

        this.companyKantorService.getCompanyKantors().subscribe(data => {
            this.listKantor = data.data
        })

        this.vendorVendorService.getVendorVendors().subscribe(data => {
            this.listVendor = data.data
        })
    }

    getData() {
        this.spinner.show()
        this.gaPembelianService.getGaPembeliansId(this.id_pembelian).subscribe(
            data => {
                this.dataForm.setValue(data.data);
                this.getListItem()
                this.spinner.hide()

                this.singleUploadService.param.next({
                    for_module: "ga_pembelian",
                    for_table: "erp_asset_ga.pembelian",
                    for_module_id: data.data.id_pembelian

                })
            }
        )
    }

    getListItem() {
        let filter = {
            id_pembelian: this.id_pembelian
        }
        this.gaPembelianItemService.getGaPembelianItems(JSON.stringify(filter)).subscribe(
            data => {
                this.list_item = data.data
            }
        )
    }

    id_model: string = null;
    add_item() {
        const drawerRef = this.drawerService.create<SearchItemComponent, { value: string }, string>({
            nzTitle: 'Search Model',
            nzContent: SearchItemComponent,
            nzWidth: '900px',
            nzContentParams: {
                value: 'arifan'
            }
        });

        drawerRef.afterOpen.subscribe(() => {
            console.log('Drawer(Component) open');
        });

        drawerRef.afterClose.subscribe(data => {

            if (typeof data === 'string') {
                this.id_model = data;
                this.beforeModel()
            }
        });
    }

    dataItem: any = {};

    beforeModel() {
        console.log("panggie ko")
        this.assetModelxService.getAssetModelxesId(this.id_model).subscribe(
            data => {
                this.dataItem.keyword = data.data.keyword;
                this.dataItem.part_number = data.data.part_number;
                this.dataItem.id_model = data.data.id_model;
                this.dataItem.qty = 1;
                this.dataItem.harga_satuan = 0;

                console.log(this.dataItem)
            }
        )
    }

    simpan_item() {
        if (this.dataItem.qty < 1) {
            alert('tidak boleh kosong');
            return false;
        }

        let param: Pembelian_item = {
            id_model: this.dataItem.id_model,
            qty: this.dataItem.qty,
            harga_satuan: this.dataItem.harga_satuan,
            id_pembelian: this.id_pembelian
        };

        this.spinner.show()
        //simpan ka server
        this.gaPembelianItemService.postGaPembelianItems(param).subscribe(
            data => {
                this.id_model = null;
                this.spinner.hide();
                this.getListItem()
            },
            err => {
                this.spinner.hide();
                this.pesan.pesanWarningForm('Dupikat pembelian Item!')
            }
        )
    }

    deleteItem(id) {
        this.spinner.show()
        this.gaPembelianItemService.deleteGaPembelianItemsId(id).subscribe(
            data => {
                this.getListItem()
                this.spinner.hide()
            }
        )
    }

    dataForm: FormGroupTypeSafe<Pembelian>
    form() {
        this.dataForm = this.fb.group<Pembelian>({
            id_pegawai: new FormControl(''),
            id_vendor: new FormControl(''),
            id_pembelian: new FormControl(''),
            id_dokumen: new FormControl(''),
            id_kantor: new FormControl(''),
            id_user: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            validasi: new FormControl(''),
            biaya_diminta: new FormControl(''),
            biaya_kembali: new FormControl(''),
            biaya_realisasi: new FormControl(''),
            tanggal_direquest: new FormControl(''),
            tanggal_invoice: new FormControl(''),
            tanggal_pembelian: new FormControl(''),
            no_invoice: new FormControl(''),
            nama_toko: new FormControl(''),
            alamat_toko: new FormControl(''),
            keterangan: new FormControl(''),
            id_data_flow: new FormControl(''),
            id_data_flow_detail: new FormControl(''),
            status: new FormControl(''),
            posisi: new FormControl(''),
            nomor: new FormControl(''),
        })
    }

    getValue(key) {
        return this.dataForm.get(key).value
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        if (this.list_item.length < 1) {
            this.pesan.pesanWarningForm('Anda harus pilih Item Pembelian!')
            this.spinner.hide()
            return
        }

        this.dataPembelian = this.dataForm.value
        this.gaPembelianService.putGaPembeliansId({ id: this.id_pembelian, body: this.dataPembelian }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            },
            err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            },
        )
    }

    goToBack() {
        this.router.navigate(['/asset/management/purchase/list'])
    }

}

