import { StandarApprovalService, standarFlow } from 'src/app/components/approval/standar/standar-approval.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { CompanyKantorService, DirektoriPegawaiService } from 'src/app/sdk/hr/services';
import { VendorVendorService, AssetModelxService, GaPembelianService, GaPembelianItemService } from 'src/app/sdk/asset/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Pegawai_master, Kantor } from 'src/app/sdk/hr/models';
import { Modelx, Vendor, Pembelian, Pembelian_item } from 'src/app/sdk/asset/models';
import { ToolsLogFlowService } from 'src/app/sdk/user/services';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private gaPembelianService: GaPembelianService,
        private vendorVendorService: VendorVendorService,
        private gaPembelianItemService: GaPembelianItemService,
        private direktoriPegawaiService: DirektoriPegawaiService,
        private companyKantorService: CompanyKantorService,
        private assetModelxService: AssetModelxService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private toolsLogFlowService: ToolsLogFlowService,
        private standarApprovalService: StandarApprovalService,
    ) { }

    dataPembelian: Pembelian = {}
    list_item: Pembelian_item[] = []
    id_pembelian: string = ''

    dataPegawai: Pegawai_master = {}
    dataKantor: Kantor = {}
    dataVendor: Vendor = {}

    ngOnInit() {
        this.activatedRoute.paramMap.subscribe(
            params => {
                console.log(params.get('id_pembelian'))
                if (params.get('id_pembelian')) {
                    this.id_pembelian = params.get('id_pembelian')
                }

                this.getDetailPembelian()
                this.getDetailItem()
                this.getLog()

            }
        )

        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getDetailPembelian()
                    this.getDetailItem()
                    this.getLog()
                }
            }
        )


    }
    params: standarFlow = null;
    id_data_flow: string = ''
    getDetailPembelian() {
        this.spinner.show()
        this.gaPembelianService.getGaPembeliansId(this.id_pembelian).subscribe(
            data => {
                this.dataPembelian = data.data;
                this.getPegawai(data.data.id_pegawai);
                this.getkantor(data.data.id_kantor);
                this.getVendor(data.data.id_vendor);
                this.spinner.hide()

                this.params = {
                    id: data.data.id_pembelian,
                    id_data_flow_detail: data.data.id_data_flow_detail,
                    id_data_flow: data.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_pembelian"
                }
                this.standarApprovalService.param.next(this.params)
                this.spinner.hide()

            }
        )
    }

    getPegawai(id_pegawai) {
        this.direktoriPegawaiService.getDirektoriPegawaisId(id_pegawai).subscribe(
            data => {
                this.dataPegawai = data.data;
            }
        )
    }
    getkantor(id_kantor) {
        this.companyKantorService.getCompanyKantorsId(id_kantor).subscribe(
            data => {
                this.dataKantor = data.data;
            }
        )
    }
    getVendor(id_vendor) {
        this.vendorVendorService.getVendorVendorsId(id_vendor).subscribe(
            data => {
                this.dataVendor = data.data;
            }
        )
    }

    getDetailItem() {
        this.gaPembelianItemService.getGaPembelianItems(JSON.stringify({ id_pembelian: this.id_pembelian })).subscribe(
            res => {
                this.list_item = res.data;
                this.spinner.hide()
            }
        )
    }

    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_pembelian })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }


    goToBack() {
        this.router.navigate(['/ga/asset/module/purchase/list'])
    }


}
